@import url(https://fonts.googleapis.com/css?family=Fauna+One|Oxygen:400,300,700);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.NoteForm {
    background-color: #1F1F1F;
    flex-direction: column;
    flex: 1 1;
    padding: 0 4rem;
    height: 100%
  }
  @media (max-width: 800px) {
    .NoteForm {
      flex-direction: column;
      flex: 1 1;
      padding: 0 4rem;
      margin-bottom: 3rem;
      height: 100%;
      overflow-y : scroll !important;
      touch-action: cross-slide-y !important;
      -webkit-overflow-scrolling: touch;
    }
  }

  @media screen and (max-width:1000px)and (orientation: landscape){
    .NoteForm {
      height: 100%;
      overflow-y : scroll !important;
      touch-action: cross-slide-y !important;
      -webkit-overflow-scrolling: touch;
      
     }
  }
  .NoteForm .form-actions {
    padding-top: 2rem;
    margin-left: -2rem;
    display: flex;
    align-content: center;
  }
  .NoteForm button {
    border: none;
    outline: none;
    background: none;
    padding: 0;
  }
  .NoteForm button[type=submit] {
    background-color: #008bf8;
    border-radius: 4px;
    color: #fff;
    font-size: 1.4rem;
    padding: 1rem;
  }
  .NoteForm button i.fa {
    color: #999;
    font-size: 2rem;
    margin-top: 2rem;
    margin-left: 2rem;
  }
  .NoteForm select {
    color: #999;
    background-color: #1F1F1F;
    position: relative;
    margin-left: auto;
    font-size: 2rem;
    margin-top: 2rem;
  }
  .NoteForm form {
    margin: 0 auto;
    /* max-width: 80rem; */
  }
  
  .NoteForm input[name=title] {
    background-color: #1F1F1F;
    border: none;
    font-size: 200%;
    font-family: Fauna One;
    color: grey;
    font-weight: 400;
    width: 100%;
    outline: none;
  }
  

  .NoteForm textarea {
    background-color: #2a2a2a;
    border-color: gray;
    border-width: 2px;
    color: white;
    width: 100%;
    height: calc((100vh - 140px)*8/10);
    font-size: 1em;
  }


  @media (max-width: 800px) and (orientation :landscape){
    .NoteForm input[name=title]{
        font-size: 100%;
    }
    .NoteForm textarea {
        width: 100%;
        height: 20rem;
        touch-action: cross-slide-y;
    }
  }
.NoteList {
    background-color: #2a2a2a;
    flex-direction: column;
    flex: 1 1;
    border-left: 2px solid gray;
    border-right: 2px solid gray;
    height: 100%;
    /* touch-action: cross-slide-y; */
  }
  @media (max-width: 800px) {
    .NoteList {
      flex-direction: column;
      flex: 1 1;
      overflow: hidden;
    }
  }

  /* @media screen and (max-width:800px){
    .NoteList {
      overflow-y : scroll !important;
      -webkit-overflow-scrolling: touch;
      touch-action: cross-slide-y !important;
     }
  } */

  

  .NoteList h3 {
    color: white;
    text-transform: uppercase;
    font-size: 2rem;
    font-family: Oxygen;
    font-weight: 300;
    letter-spacing: 3px;
    margin: 20px 2rem;
  }
  .NoteList ul#notes {
    border-top: 2px solid gray;
    /* overflow-y: scroll; */
    overflow-y: hidden;
    height: calc((100vh - 140px)*3/4);
    list-style: none;
    margin-top: 1em;
    margin-bottom: 1em;
    padding: 0em;
    width: 100%;
    color: #999;
  }
  width 
  ::-webkit-scrollbar {
    width: 20px;
  }
 
  
  Track 
  ::-webkit-scrollbar-track {
    box-shadow: inset 0 0 5px grey; 
    border-radius: 10px;
  }
 
   
  Handle 
  ::-webkit-scrollbar-thumb {
    background: gray; 
    border-radius: 10px;
  }
 
  
  Handle on hover 
  ::-webkit-scrollbar-thumb:hover {
    background: #b30000; 
  } 
 
  
  
  .NoteList ul#notes a,
  .NoteList ul#notes a:active,
  .NoteList ul#notes a:hover,
  .NoteList ul#notes a:link,
  .NoteList ul#notes a:visited {
    color: inherit;
    text-decoration: none;
  }
  .NoteList ul#notes>li {
    height: calc((100vh - 140px)*3/4);
    padding-bottom: 3rem;
    font-size: 90%;
    cursor: pointer;
    overflow: hidden;
  }
  @media (max-width: 800px) {
    .NoteList ul#notes>li {
      height: calc((100vh - 140px)*3/4);
      overflow-y: hidden;
    }
  }
  .NoteList ul#notes>a,
  .NoteList ul#notes>a * {
    transition: background-color .1s ease-in-out, color .1s ease-in-out;
  }
  .NoteList ul#notes li .note {
    border-top: 1.4px solid gray;
    margin: 0 2rem;
    padding: 1rem 4px;
  }
  .NoteList ul#notes a:first-of-type li .note {
    border-top: none;
  }
  /* .NoteList ul#notes li:hover {
    background-color: grey;
  }
  .NoteList ul#notes li:hover,
  .NoteList ul#notes li:hover .note * {
    color: #fff!important;
    text-decoration: none !important;
  } */
  .NoteList ul#notes li:hover .note {
    border-color: transparent;
  }
  .NoteList ul#notes li .note-title {
    color: #4a4a4a;
    font-family: Fauna One;
    font-size: 120%;
    font-weight: 400;
    white-space: nowrap;
    overflow-x: hidden;
    /* -o-text-overflow: ellipsis;
    text-overflow: ellipsis; */
  }
  .NoteList ul#notes li .note-body {
    height: calc((100vh - 140px)*3/4);
    /* overflow: hidden; */
    margin-top: .5rem;
  }
  .NoteList ul#notes li .note-body * {
    font-size: 1em!important;
    margin: 0!important;
    padding: 0!important;
    color: #999!important;
    background: none;
    border: none;
  }
  .NoteList ul#notes li .note {
    display: block;
    height: calc((100vh - 140px)*4/10);
    padding: 1em;
    text-decoration: none;
  }

  .NoteList ul#notes li .note-body textarea{
    background-color: #1F1F1F;
    border-color: grey;
    border-width: 4px;
    color: white;
    width: 100%;
    height: calc((100vh - 140px)*3/10);
    font-size: 1em;
  }


  @media (max-width: 800px) and (orientation :landscape){
    .NoteList .note-body textarea {
        width: 100%;
        background-color: #2a2a2a;
        border-color: #1F1F1F;
        height: 20rem;
        touch-action: cross-slide-y;
    }
  }
.Resizer {
    background: #000;
    opacity: 0.2;
    z-index: 2;
    box-sizing: border-box;
    -moz-background-clip: padding;
    -webkit-background-clip: padding;
    background-clip: padding-box;
  }
  
  .Resizer:hover {
    transition: all 2s ease;
  }
  
  .Resizer.horizontal {
    height: 11px;
    margin: -5px 0;
    /* border-top: 5px solid rgba(255, 255, 255, 0); */
    border-top: 2px solid black;
    border-bottom: 2px solid black;
    /* border-bottom: 5px solid rgba(255, 255, 255, 0); */
    cursor: row-resize;
    width: 100%;
  }
  
  .Resizer.horizontal:hover {
    /* border-top: 5px solid rgba(0, 0, 0, 0.5); */
    border-top: 2px solid black;
    /* border-bottom: 5px solid rgba(0, 0, 0, 0.5); */
    border-bottom: 2px solid black;
  }
  
  .Resizer.vertical {
    width: 11px;
    margin: 0 -5px;
    border-left: 5px ridge rgba(255, 255, 255, 0);
    border-right: 5px ridge rgba(255, 255, 255, 0);
    cursor: col-resize;
  }
  
  .Resizer.vertical:hover {
    border-left: 5px solid rgba(0, 0, 0, 0.5);
    border-right: 5px solid rgba(0, 0, 0, 0.5);
  }
  .Resizer.disabled {
    cursor: not-allowed;
  }
  .Resizer.disabled:hover {
    border-color: transparent;
  }
* {
  -webkit-box-sizing: border-box;
  -webkit-tap-highlight-color:transparent;
  box-sizing: border-box;
  
}

